import React from 'react'
// styles
require('../../scss/components/CTA.scss')
//data
const propertyDomains = require('../../data/propertyDomains.json')

export default function CTA(props) {

  function getHostName(url) {
      var match = url.match(/:\/\/(www[0-9]?\.)?(.[^/:]+)/i);
      if (match != null && match.length > 2 && typeof match[2] === 'string' && match[2].length > 0) {
      return match[2];
      }
      else {
          return null;
      }
  }

  let url = props.encodedUrl
  // let url = 'https://preprod-uw.democratandchronicle.com/preview/story/longform/6004979002/?d=1603371197484&beta=false'

  if (url) {
    let hostName = getHostName(url).replace('preprod-uw.', '')
    console.log(hostName)
    let propertyInfo = propertyDomains.filter(d => d.url.includes(hostName))

    if (propertyInfo.length) {
      return(
        <div class="CTA">
          <h4>Do you find this valuable?</h4>
          <p>Our Voters Guide is available to all readers free of charge, but public service journalism like this takes time and resources. <a href={propertyInfo[0].subscribePage} target='_blank' className='g-link'>Please consider supporting our work.</a></p>
        </div>
      )
    } else {
      return('')
    }
  } else {
    return('')
  }


}
